<template>
  <v-dialog v-model="rank" fullscreen>
    <div id="rank" :style="{ backgroundColor: color }">
      <v-container fluid>
        <v-layout column class="bc my-1">
          <v-layout class="mb-3" align-center>
            <v-flex class="text-xs-left">
              <div class="mx-3 mt-1 mb-0 headline white--text">
                <strong> : {{ myRank }}</strong>
              </div>
            </v-flex>
            <v-spacer></v-spacer>
            <v-flex class="text-xs-right ma-0">
              <v-btn icon @click="goBack">
                <v-icon class="white">exit_to_app</v-icon>
              </v-btn>
            </v-flex>
          </v-layout>
          <v-data-table
            class="elevation-1 mx-3 my-0"
            :headers="headers"
            :items="rankers"
            :pagination.sync="pagination"
            rows-per-page-text=" "
            no-data-text="  ."
            no-results-text=" ."
          >
            <template slot="items" slot-scope="props">
              <td class="text-xs-center">{{ props.item.rank }}</td>
              <td class="text-xs-center">{{ props.item.nick }}</td>
              <td class="text-xs-center">{{ props.item.score }}</td>
            </template>
          </v-data-table>
          <v-switch v-model="soundOn" class="ml-3">
            <template slot="label">
              <span class="white--text">{{ soundOn ? " " : " " }}</span>
            </template>
          </v-switch>
        </v-layout>
      </v-container>
    </div>
  </v-dialog>
</template>

<script>
export default {
  props: {
    rank: Boolean
  },
  data() {
    return {
      color: "#212e39",
      myRank: 0,
      pagination: {
        descending: false,
        rowsPerPage: 10,
        sortBy: "rank"
      },
      headers: [
        { text: "", align: "center", sortable: false, value: "rank" },
        { text: "", align: "center", sortable: false, value: "nick" },
        { text: "", align: "center", sortable: false, value: "score" }
      ],
      rankers: []
    };
  },
  computed: {
    soundOn: {
      set(soundOn) {
        this.$store.commit("setSound", soundOn);
      },
      get() {
        return this.$store.getters.sound;
      }
    }
  },
  created() {
    this.getRanks();
  },
  methods: {
    goBack: function() {
      this.$emit("update:rank", false);
    },
    getRanks: function() {
      fetch("https://www.hoopeeworld.co.kr/goldrush/getRanks.php", {
        method: "GET"
      })
        .then(res => res.json())
        .then(data => {
          if (data.result == 0) {
            this.rankers = data.data.map((ranker, i) => {
              return {
                rank: i + 1,
                id: ranker.ac_idx,
                nick: ranker.name,
                score: ranker.score
              };
            });
            console.log(this.rankers);
            this.myRank = this.rankers.filter(
              ranker => ranker.id == this.$store.getters.ac_idx
            )[0].rank;
            console.log(this.myRank);
          }
        });
    }
  }
};
</script>

<style>
#rank {
  height: 100%;
}
</style>
